/* 1. Use a more-intuitive box-sizing model */
*,
*::before,
*::after {
  box-sizing: border-box;
}

/* 2. Remove default margin */
* {
  margin: 0;
}

/* 3. Enable keyword animations */
@media (prefers-reduced-motion: no-preference) {
  html {
    interpolate-size: allow-keywords;
  }
}

body {
  /* 4. Add accessible line-height */
  line-height: 1.5;
  /* 5. Improve text rendering */
  -webkit-font-smoothing: antialiased;
}

/* 6. Improve media defaults */
img,
picture,
video,
canvas,
svg {
  display: block;
  max-width: 100%;
}

/* 7. Inherit fonts for form controls */
input,
button,
textarea,
select {
  font: inherit;
}

/* 8. Avoid text overflows */
p,
h1,
h2,
h3,
h4,
h5,
h6 {
  overflow-wrap: break-word;
}

/* 9. Improve line wrapping */
p {
  text-wrap: pretty;
}
h1,
h2,
h3,
h4,
h5,
h6 {
  text-wrap: balance;
}

/*
  10. Create a root stacking context
*/
#root,
#__next {
  isolation: isolate;
}

/* Custom CSS Rules */

:root {
  --gradient-start: #3572e0;
  --gradient-end: #9445f0;
  --card-bg: rgba(255, 255, 255, 0.15);
  --text-color: white;
  --primary: #2323e4d7;
  --accent: #ff2a85;
}

html {
  height: 100vh;
}

body {
  display: grid;
  justify-content: center;
  align-items: center;
  padding-top: 52px;
  height: 100%;
  background: linear-gradient(
    135deg,
    var(--gradient-start),
    var(--gradient-end)
  );
  color: var(--text-color);
  font-size: 18px;
}

/* Layout */

.form {
  display: flex;
  gap: 20px;
}

/* Search Bar */
.input-search {
  backdrop-filter: blur(20px);
  background-color: var(--card-bg);
  border: none;
  border-radius: 25px;
  flex: 1;
  padding: 0 16px 0 16px;
  color: white;
  font-size: 20px;
  transition: all 0.35s ease;
}

.input-search:focus {
  outline: none;
  box-shadow:
    rgba(50, 50, 93, 0.25) 0px 13px 27px -5px,
    rgba(0, 0, 0, 0.3) 0px 8px 16px -8px;
  background-color: hsla(0, 0%, 100%, 0.12);
}

input::placeholder {
  color: hsla(0, 0%, 100%, 0.65);
}

.submit-btn {
  display: flex;
  color: white;
  background-color: #3a86ff;
  padding: 16px;
  border-radius: 50px;
  border: none;
  transition: all 0.3s ease;
}

.submit-btn:hover {
  cursor: pointer;
  background-color: hsla(0, 0%, 100%, 0.28);
}

.submit-btn:active {
  transform: scale(0.8);
}

.temperature-scale {
  display: grid;
  grid-auto-flow: column;
  place-items: center;
  column-gap: 8px;
  background-color: rgb(3, 3, 41);
  padding: 4px 12px;
  border-radius: 25px;
  width: max-content;
  backdrop-filter: blur(20px);
  background-color: var(--card-bg);
}

/* Toggle Switch */

.switch {
  position: relative;
  display: inline-block;
  width: 56px;
  height: 30px;
}

/* Hide Default checkbox */
.switch input {
  opacity: 0;
  width: 0;
  height: 0;
}

/* The slider */
.slider {
  position: absolute;
  cursor: pointer;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: hsla(0, 0%, 100%, 0.45);
  -webkit-transition: 0.4s;
  transition: 0.4s;
}

.slider:before {
  position: absolute;
  content: '';
  height: 22px;
  width: 22px;
  left: 4px;
  bottom: 4px;
  background-color: white;
  -webkit-transition: 0.4s;
  transition: 0.4s;
}

input:checked + .slider {
  background-color: hsla(0, 0%, 100%, 0.25);
}

input:focus + .slider {
  box-shadow: 0 0 1px #2196f3;
}

input:checked + .slider:before {
  -webkit-transform: translateX(26px);
  -ms-transform: translateX(26px);
  transform: translateX(26px);
}

/* Rounded sliders */
.slider.round {
  border-radius: 25px;
}

.slider.round:before {
  border-radius: 50%;
}

/* Weather Content */
.weather-card {
  min-width: 40%;
  border-radius: 25px;
  display: grid;
  grid-template-rows: max-content 1fr;
  grid-template-columns: max-content max-content;
  row-gap: 20px;
  column-gap: 20px;
  /* backdrop-filter: blur(10px);
  background-color: var(--card-bg); */
}

.error-msg {
  grid-area: 2 / 1 / 3 / 3;
  display: none;
}

.loading-container {
  grid-area: 2 / 1 / 3 / 3;
  font-size: 24px;
  display: none;
  justify-content: center;
  row-gap: 12px;
}

/* Weather Overview */
.overview {
  grid-area: 2 / 1 / 3 / 3;
  display: grid;
  grid-template-areas:
    'location'
    'groups';
  row-gap: 20px;
  /* border: 1px solid black; */
}

.location {
  grid-area: location;
  justify-self: center;
  color: var(--color-dark-blue);
  font-size: 32px;
}

.now-text {
  font-size: 24px;
  font-weight: 600;
}

.current-temp {
  color: var(--color-primary-blue);
  font-size: 40px;
  margin-bottom: -8px;
}

.feels-like {
  color: var(--color-mid-blue);
  font-size: 20px;
}

.condition-status {
  color: var(--color-mid-blue);
  font-size: 20px;
}

.condition-status-icon {
  font-size: 84px;
}

.groups {
  grid-area: groups;
  display: grid;
  grid-template-columns: 1fr 1fr;
  justify-items: center;
}

.overview-right {
  display: grid;
  justify-items: center;
}

.overview-left {
  display: grid;
  grid-template-rows: max-content max-content max-content;
}

/* Weather Details */

.details div div {
  /* border: 1px solid black; */
  border-radius: 12px;
  padding: 8px;
  display: grid;
  place-items: center;
  grid-template-rows: max-content max-content max-content;
  background-color: var(--card-bg);
  transition: all 0.3s ease;
}

.details div div:hover {
  cursor: pointer;
  box-shadow:
    rgba(0, 0, 0, 0.1) 0px 20px 25px -5px,
    rgba(0, 0, 0, 0.04) 0px 10px 10px -5px;
  background-color: hsla(0, 0%, 100%, 0.13);
  transform: translateY(-2px);
}

.details {
  grid-area: 3 / 1 / 4 / 3;
  display: grid;
  grid-template-columns: 1fr 1fr;
  column-gap: 16px;
}

.details-left,
.details-right {
  display: grid;
  row-gap: 16px;
  padding: 16px;
  box-sizing: border-box;
}

.details-left {
  padding-right: 0;
}

.details-right {
  padding-left: 0;
}

.details iconify-icon {
  font-size: 32px;
}
